import { SHA256 } from 'crypto-js';

export const Users = ["", "", "", "", "", "", ""]
export const dateRegexp = /^2[0-9]{3}[0-1]{1}[0-9]{1}[0-3]{1}[0-9]{1}/g
export const getRandomTarget = (list, name, value) => list[SHA256(value + name).toString().split('').map(_ => _.charCodeAt()).reduce((a,b) => a+b,0) % list.length]